@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap);
*{margin:0;padding:0;font-family:"Jost", sans-serif}body{background:#f3f2ef;display:flex;justify-content:center;align-items:center;flex-wrap:wrap;height:100vh;width:100vw;font-family:"Jost", sans-serif}html{font-size:100%;height:100%}html{background:#eee}.box{width:25vw;height:60vh;border-radius:0.5rem;box-shadow:0 0.2rem 3rem rgba(0,0,0,0.2);background:#a5bbdd;position:relative;overflow:hidden;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);min-width:20rem;min-height:35rem}.wave{opacity:0.3;position:absolute;top:120%;left:50%;background:#fff;width:50rem;height:50rem;margin-left:-25rem;margin-top:-20rem;-webkit-transform-origin:50% 48%;transform-origin:50% 48%;border-radius:43%;-webkit-animation:drift 3000ms infinite linear;animation:drift 3000ms infinite linear;z-index:1}.wave.-three{-webkit-animation:drift 7000ms infinite linear;animation:drift 7000ms infinite linear;z-index:2 !important;opacity:0.2}.wave.-two{-webkit-animation:drift 7000ms infinite linear;animation:drift 7000ms infinite linear;opacity:0.1;z-index:3 !important}.box::after{content:"";display:block;left:0;top:0;width:100%;height:100%;z-index:11;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}@-webkit-keyframes drift{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}from{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes drift{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}from{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.info{position:absolute;display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;height:60vh;z-index:4}.location{margin-top:-5.5rem;text-align:center;font-weight:800;font-size:5rem;text-transform:capitalize}.fa-street-view{-webkit-animation:rotates 3s linear infinite ease;animation:rotates 3s linear infinite ease}@-webkit-keyframes rotates{from{-webkit-transform:translateX(-0.5rem);transform:translateX(-0.5rem)}from{-webkit-transform:translateX(0.5rem);transform:translateX(0.5rem)}}@keyframes rotates{from{-webkit-transform:translateX(-0.5rem);transform:translateX(-0.5rem)}from{-webkit-transform:translateX(0.5rem);transform:translateX(0.5rem)}}.temp{margin-top:2.5rem;text-align:center;font-size:3rem}.tempmin_max{text-align:center;margin-top:0.3rem;font-weight:300;font-size:1.2rem;color:#57606f}.fas{font-size:4.5rem;padding-right:16px;-webkit-animation:fas-anime 3s linear infinite alternate;animation:fas-anime 3s linear infinite alternate}@-webkit-keyframes fas-anime{from{-webkit-transform:scale(1.1);transform:scale(1.1)}to{-webkit-transform:scale(0.85);transform:scale(0.85)}}@keyframes fas-anime{from{-webkit-transform:scale(1.1);transform:scale(1.1)}to{-webkit-transform:scale(0.85);transform:scale(0.85)}}body>span{width:100vw;text-align:center;color:grey}.inputData{width:100%;height:10vh;display:flex;justify-content:center;align-items:center;margin-top:20px;margin-bottom:20px}.inputField{width:50%;margin:auto;border-radius:5rem;padding:1rem 1rem;outline:none;font-size:1.5rem}.errorMsg{text-align:center;font-size:2rem;margin:2rem auto}

